<template>
    <div class="post">

        <div class="post__header">

            <div class="post__user">
                <div class="user">
                    <a href="#" class="user__avatar">
                        <img :src="user.image" alt="">
                    </a>
                    <a href="#" class="user__name" :style="{color:'green'}">
                        {{user.name}} {{user.family}}
                    </a>
                </div>

            </div>

            <div class="post__links">
                <a href="#" class="edit-link"
                   v-on:click.prevent="$emit('remove',post.id)"
                >
                    <i class="far fa-trash-alt"></i>
                    Delete
                </a>


                <a href="edit.html" class="edit-link">
                    <i class="fas fa-pencil-alt"></i>
                    Edit
                </a>

            </div>
        </div>

        <div class="post__img">
            <img
                    v-if="content.image.length == 1"
                    :src="content.image" alt="Photo">
            <div class="slider owl-theme" v-else>
                <img
                        v-for="(image,index) of content.image"
                        :src="image" alt="Photo"
                        :key="index"
                >
            </div>
        </div>

        <!-- post__data  -->
        <div class="post__data">

            <div class="post__description"
            >
                {{content.description}}
            </div>

            <div class="post__hashtags">
                <a href="#"
                   v-for="(tag,index) of content.tags"
                   v-bind:key="index"
                >
                    {{tag}}
                </a>
            </div>
        </div>
        <Comment
                v-for="(comment,index) of comments"
                :key="index"
                :comment="comment"
        />

        <div class="post__comment-form">
            <div class="comment-form">
                <textarea class="comment-form__textarea" placeholder="Добавить комментарий"></textarea>
                <button disabled class="comment-form__button">Опубликовать</button>
            </div>
        </div>
    </div>
</template>


<script>
import Comment from './Comment.vue'
    export default {
        props:['post'],
        data(){
            return {
                user: this.post.user,
                content: this.post.content,
                comments: this.post.comments,
            }
        },
        components:{
            Comment
        },
        filters:{

        }


    }
</script>

<style>
    .red{
        color: red;
    }
</style>
