

<template>
  <div id="app">
    <Header/>
    <div class="container">
    <Post
            v-for="(post) of data.posts"
            :key="post.id"
            :post="post"
            v-on:remove="removePost"

    />

    </div>

  </div>
</template>

<script>
import Data from './fakeDate.json'
import Header from './components/Header'
import Post from './components/Post'
export default {
  name: 'app',
  components:{
    Header, Post
  },
  data(){
    return {
      data: Data
    }
  },
  methods:{
    removePost(i){
      this.data.posts = this.data.posts.filter(e=>e.id !== i)
    }

  },

}

</script>


